import logo from './logo.svg';
import './App.css';
import 'dayjs/locale/es';
import { useState } from 'react';
import { Button, Radio, Group, RangeSlider, Checkbox, Container, NumberInput} from '@mantine/core';
import { DatePicker } from '@mantine/dates';
const dayjs = require('dayjs')
function App() {
  const [tarifaHoraria, setTarifaHoraria] = useState('288');
  const [fechas, setFechas] = useState([]);
  const [datosFechas, setDatosFechas] = useState([]);
  const [datosFaltante, setDatosFaltante] = useState({faltante:0, pesoCajon:0, precioKG:0, precioDolar:275});
  const UpdateFechas = (newFechas) => {
    const newDatosFechas = [];
    for(const fecha of newFechas) {
      let found = false
      for(const datos of datosFechas) {
        if(datos.fecha === fecha){ newDatosFechas.push(datos); found=true; break;}
      }
      if(!found) {
        newDatosFechas.push({fecha:fecha, horarios:{inicio:0,final:24},feriado:true}); 
      }
    }
    setFechas(newFechas);
    setDatosFechas(newDatosFechas);
  }

  const HandleFeriado = (fecha, valor) => {
    // 1. Encontrar en los datos la fecha indicada
    const index = datosFechas.findIndex((dato) => dato.fecha === fecha);
    // 2. Mark the todo as complete
    const datosNuevos = {...datosFechas[index], feriado: valor};
    // 3. Update the todo list with the updated todo
    const arr = [
      ...datosFechas.slice(0, index),
      datosNuevos,
      ...datosFechas.slice(index + 1)
    ];
    setDatosFechas(arr);
  };

  const HandleHorarios = (fecha, valor) => {
    // 1. Encontrar en los datos la fecha indicada
    const index = datosFechas.findIndex((dato) => dato.fecha === fecha);
    // 2. Mark the todo as complete
    const datosNuevos = {...datosFechas[index], horarios: {inicio:valor[0], final:valor[1]}};
    // 3. Update the todo list with the updated todo
    const arr = [
      ...datosFechas.slice(0, index),
      datosNuevos,
      ...datosFechas.slice(index + 1)
    ];
    setDatosFechas(arr);
  };
  const CalcularValorCarga = () =>datosFaltante.faltante*datosFaltante.pesoCajon*datosFaltante.precioKG;

  const CalcularValorHoras = (dias) => {
    let suma = 0;
    for(const dia of datosFechas) {
      suma += CalcularValorDia(dia)
    }
    return suma;
  }
  const CalcularValorDia = (dia) => {
    let horasNormal = 0;
    let horas40 = 0;
    let horas80 = 0;
    if(dia.feriado) {
      horas80 += dia.horarios.final- dia.horarios.inicio;
    }else {
      for(let i = dia.horarios.inicio; i < dia.horarios.final; i++) {
        if(i < 7 || (i>= 11 && i < 13) || i>=17) {
          horas40++;
        }else{
          horasNormal++;
        }
      }
    }
    let tarifa = Number(tarifaHoraria);
    let valorHoras = horasNormal*tarifa;
    valorHoras += horas40*tarifa + horas40*tarifa*0.4;
    valorHoras += horas80*tarifa + horas80*tarifa*0.8;
    return valorHoras;
  }

  const CalcularValor = ()=>{
    const valorCarga = datosFaltante.faltante*datosFaltante.pesoCajon*datosFaltante.precioKG;
    let valorHoras = 0;
    let horasNormal = 0;
    let horas40 = 0;
    let horas80 = 0;
    for(const dia of datosFechas) {
      if(dia.feriado) {
        horas80 += dia.horarios.final- dia.horarios.inicio;
      }else {
        for(let i = dia.horarios.inicio; i < dia.horarios.final; i++) {
          if(i < 7 || (i>= 11 && i < 13) || i>=17) {
            horas40++;
          }else{
            horasNormal++;
          }
        }
      }
    }
    let tarifa = Number(tarifaHoraria);
    valorHoras += horasNormal*tarifa;
    valorHoras += horas40*tarifa + horas40*tarifa*0.4;
    valorHoras += horas80*tarifa + horas80*tarifa*0.8;
    return (valorHoras+valorCarga)*datosFaltante.precioDolar;
  }

  return (
    <div className="App">
      <h1>SICONARA</h1>
      <h2>Calculadora Remolcadores</h2>
    <p>Tarifa Horaria</p>
      <Group position='center'>
    
    <Radio.Group
      value={tarifaHoraria}
      onChange={(v) => {console.log(v);setTarifaHoraria(v)}}
      name="Tarifa Horaria"
      label=""
      
    ><Group>
      <Radio value='288'label='<=299HP (288$ USD)'/>
      <Radio value='503'label='300-1.099HP (503$ USD)'/>
      <Radio value='794'label='300-1.099HP (794$ USD)'/>
      <Radio value='1061'label='>=2000HP (1.061$ USD)'/>
      </Group>
    </Radio.Group>

   
    </Group>
    <p>Fechas Trabajadas</p>
    <Group position='center'>
    <DatePicker on type="multiple" value={fechas} onChange={UpdateFechas} allowDeselect={true} locale='es'/>
    </Group>
    <p>Horarios Trabajados</p>
    <Container size='sm' style={{paddingTop:'1em'}}>
      {datosFechas.map(d =>{
        return (
          <Group grow style={{padding:'1em'}}>
            <span>{d.fecha?dayjs(d.fecha).format('DD/MM/YYYY'):''}</span>
            <RangeSlider  
              labelAlwaysOn 
              minRange={1} 
              min={0} 
              max={24} 
              step={1} 
              label={(value) => `${value}:00hs`} 
              value={[d.horarios.inicio, d.horarios.final]}
              onChange={(v) => HandleHorarios(d.fecha, v)}
            />
            <Checkbox 
            label='Feriado' 
            checked={d.feriado} 
            onChange={
              (v) => {
                HandleFeriado(d.fecha, v.currentTarget.checked)
              }
            }/>
            <p><em>{Number((CalcularValorDia(d)).toFixed(2)).toLocaleString('en-US')}</em>{' '}$USD</p>
          </Group>
        )
      })}
      <Group grow style={{padding:'1em'}}>
      <p>Total horas: </p><p><b>{Number((CalcularValorHoras()).toFixed(2)).toLocaleString('en-US')} $USD</b></p>
      </Group>
      <Group grow ><p>Carga Faltante(cantidad de cajones):</p>
      <NumberInput  
      hideControls 
      value={datosFaltante.faltante} 
      onChange = {v => {setDatosFaltante(old =>{return {...old, faltante:v}})}}
      />
      </Group>
      <Group grow ><p>Peso por Cajon:</p>
      <NumberInput  
      hideControls
      value={datosFaltante.pesoCajon} 
      precision={2}
      onChange = {v => {setDatosFaltante(old =>{return {...old, pesoCajon:v}})}}
      parser={(value) => value.replace(/\kg\s?|(,*)/g, '')}
        formatter={(value) =>
          !Number.isNaN(parseFloat(value))
            ? `${value} Kg`.replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ',')
            : 'Kg'
        }
      /></Group>
      <Group grow ><p>Precio por KG ($USD):</p>
      <NumberInput
        hideControls
        value={datosFaltante.precioKG} 
        precision={2}

        onChange = {v => {setDatosFaltante(old =>{return {...old, precioKG:v}})}}
        parser={(value) => value.replace(/\$\s?|(,*)/g, '')}
        formatter={(value) =>
          !Number.isNaN(parseFloat(value))
            ? `$ ${value}`.replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ',')
            : '$ '
        }
        /></Group>
        <Group grow style={{padding:'1em'}}>
         <p>Total carga faltante: </p><p><b>{Number((CalcularValorCarga()).toFixed(2)).toLocaleString('en-US')} $USD</b></p>
         </Group>
      <Group grow ><p>Precio Dolar($ARS):</p>
      <NumberInput  
      hideControls
      value={datosFaltante.precioDolar} 
      precision={2}

        onChange = {v => {setDatosFaltante(old =>{return {...old, precioDolar:v}})}}
        parser={(value) => value.replace(/\$\s?|(,*)/g, '')}
        formatter={(value) =>
          !Number.isNaN(parseFloat(value))
            ? `$ ${value}`.replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ',')
            : '$ '
        }
      /></Group>
      <Group grow >
        <p style={{fontSize:'1.5em'}}><b>Jefe:  </b>{Number((CalcularValor()*0.0371).toFixed(2)).toLocaleString('en-US')} $ARS</p>
        <p style={{fontSize:'1.5em'}}><b>1 Conductor:  </b>{Number((CalcularValor()*0.0341).toFixed(2)).toLocaleString('en-US')} $ARS</p>
      </Group>
      </Container>
    
    </div>
  );
}

export default App;
